.detail {
    position: fixed;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    top: 5%;
    width: 80vw;
    height: 90%;
    z-index: 30;
    border-radius: 20px;
    background-color: rgba(242,242,242,0.9);
}

.detail::-webkit-scrollbar {
    display: none;
}

.detail .detail__close {
    display: none;
    color: rgba(0,0,0,0.5);
}

.detail .detail__close:hover {
    cursor: pointer;
}

.detail .detail__content {
    display: block;
    text-align: center;
    padding: 50px;
    font-size: 20px;
    font-weight: 350;
}

.detail .detail__content img {
    max-width: 100%;
    margin-top: 30px;
    border-radius: 10px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.detail .detail__content h6 {
    font-style: italic;
    font-size: 16px;
    font-weight: 300;
}

.detail .detail__area {
    position: relative;
    width: 100%;
    padding-bottom: 54%;
}

.detail .detail__video {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    border: 0;
    border-radius: 10px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}