.body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.title {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    font-weight: 300;
}

.ctrl {
    display: block;
    text-align: center;
}

.ctrl__select {
    display: inline-block;
    justify-content: center;
    align-items: center;
}

.ctrl__search {
    display: inline-block;
    justify-content: center;
    align-items: center;
}

.container {
    display: flex;
    justify-content: center;
}

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80vh;
    font-weight: 300;
}

.movies {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    height: 100%;
    margin: 20px 5% 50px 5%;
}

.side .left {
	display: flex;
    position: fixed;
    text-align: center;
    cursor: pointer;
    top: 50%;
    left: 5px;
    padding: 15px;
    font-size: 16px;
    font-weight: 600;
    opacity: 0.5;
    border: 0;
    -moz-border-radius: 0 90px 90px 0;
    -webkit-border-radius: 0 90px 90px 0;
    border-radius: 0 90px 90px 0;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.side .right {
	display: flex;
    position: fixed;
    text-align: center;
    cursor: pointer;
    top: 50%;
    right: 5px;
    padding: 15px;
    font-size: 16px;
    font-weight: 600;
    opacity: 0.5;
    border: 0;
    -moz-border-radius: 90px 0 0 90px;
    -webkit-border-radius: 90px 0 0 90px;
    border-radius: 90px 0 0 90px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}