.searchbar .item {
    display: block;
    text-align: left;
}

.searchbar .item:hover {
    cursor: pointer;
    background-color: whitesmoke;
    transition: all .03s linear;
}

.item .image{
    display: inline-table;
    width: 50px;
    border-radius: 5px;
}

.item .data {
    position: absolute;
    display: inline-table;
    margin: 5px;
}

.data .title{
    display: table-cell;
    width: 180px;
    font-size: 13px;
    font-weight: 400;
}

.item .data .year{
    display: table-footer-group;
    line-height: 25px;
    font-size: 11px;
    font-weight: 300;
}